import React, { useState, useEffect, useRef } from "react";
import "./PaperEvs.css";
import "../components/FormPrint.css";
import { useSelector } from "react-redux";

import moment from "moment";
import { useReactToPrint } from "react-to-print";

const PaperEvsResult = () => {
  const { sso } = useSelector((state) => state);
  const { modal, user, evsdata } = sso;
  const { setting } = useSelector((state) => state.theme);
  const { REACT_APP_THEME_TAG: themetag } = process.env;
  const ui = setting.clients.find((r) => r.tag === themetag);
  const { REACT_APP_API_URL: API_URL } = process.env;
  const [Logo, setLogo] = useState(null);
  const [electors, setElectors] = useState([]);

  const [data, setData] = useState(null);
  const printRef = useRef();

  const handlePrint = useReactToPrint({
    content: () => printRef.current,
  });

  const getPortfolio = (name) => {
    return (
      evsdata &&
      evsdata.candidates &&
      evsdata.candidates.filter((r) => r.portfolio == name)
    );
  };

  useEffect(() => {
    import(`./../../assets/img/logo/${themetag}/logo.png`).then((src) =>
      setLogo(src.default)
    );
    modal.content && setData(modal.content);
    evsdata && evsdata.electors && setElectors(evsdata.electors);
  }, []);

  return (
    <>
      <div className="row">
        <div
          className="Box small-12 columns"
          style={{
            width: "900px",
            margin: "0px auto",
            float: "none",
            overflow: "hidden",
          }}
        >
          <div className="small-12 columns u-pr-0 right">
            <button
              onClick={handlePrint}
              className="Button u-floatRight u-mb-2 btn btn-sm btn-primary"
              style={{ float: "right" }}
            >
              &nbsp;&nbsp;PRINT&nbsp;&nbsp;
            </button>
          </div>
        </div>
      </div>
      <div className="print" ref={printRef}>
        <div className="cover">
          <content>
            {/* REGISTRATION SLIP */}
            <div style={{ marginTop: "20px" }} />
            <section className="mcover">
              <header>
                <h2 style={{ background: "#0a2f72", letterSpacing: "0.09em" }}>
                  {data && data.toUpperCase()}
                </h2>
                <h1>FINAL ELECTION RESULTS</h1>
                <h2>OFFICE OF THE ELECTORAL COMMISSIONER</h2>
                <img
                  className="hlogo"
                  src={`${API_URL}/photos/evs/?tag=logo&eid=${
                    evsdata && evsdata.id
                  }`}
                />
              </header>
              <content className="bcover">
                <table className="results">
                  {evsdata &&
                    evsdata.portfolios &&
                    evsdata.portfolios.map((row, i) => (
                      <>
                        <tr className="head">
                          <td>
                            <h3>{row.name}</h3>
                          </td>
                        </tr>
                        <tr className="body">
                          <td>
                            <div className="portfoliox">
                              {getPortfolio(row.name) &&
                                getPortfolio(row.name)
                                  .sort((a, b) => b.votes - a.votes)
                                  .map((r, j) => (
                                    <div className="candidatex">
                                      <img
                                        src={`${API_URL}/photos/evs/?tag=${r.id}&eid=${r.id}`}
                                        className="canphoto"
                                      />
                                      <div className="details">
                                        <h3>{r.name}</h3>
                                        <h4>
                                          {r.votes}{" "}
                                          <em>
                                            (
                                            {(
                                              (parseInt(r.votes) /
                                                electors.length) *
                                              100
                                            ).toFixed(1)}
                                            %)
                                          </em>
                                        </h4>
                                      </div>
                                    </div>
                                  ))}
                            </div>
                          </td>
                        </tr>
                      </>
                    ))}
                </table>
              </content>
              <footer>
                <img
                  className="flogo"
                  src={`${API_URL}/photos/evs/?tag=logo&eid=${
                    evsdata && evsdata.id
                  }`}
                />
                {/*<h4 className="ftext">UNIVERSITY OF CAPE COAST</h4>*/}
                <h4 className="ftext">ELECTION STATISTICS</h4>
                <div className="rbody">
                  <div className="rcover">
                    <h3>{(evsdata && evsdata.voters_count) || 0}</h3>
                    <p>REGISTERED</p>
                  </div>
                  <div className="rcover">
                    <h3>
                      {electors
                        ? electors.length
                        : evsdata && evsdata.electors
                        ? evsdata.electors.length
                        : 0}
                    </h3>
                    <p>TURNOUT</p>
                  </div>
                  <div className="rcover">
                    <h3>
                      {Math.max(
                        0,
                        ((evsdata && evsdata.voters_count) || 0) -
                          (electors
                            ? electors.length
                            : evsdata && evsdata.electors
                            ? evsdata.electors.length
                            : 0)
                      )}
                    </h3>
                    <p>ABSENT</p>
                  </div>
                </div>
              </footer>
            </section>

            <br />
            <br />
          </content>
        </div>
      </div>
    </>
  );
};

export default PaperEvsResult;
