import React, { Component, useState } from "react";
import "./SSOLayout.css";
import SSOTopNav from "../components/SSOTopNav";
import SSONavbar from "../components/SSONavbar";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import AlertBox from "../components/AlertBox";
import DialogBox from "../components/DialogBox";
import SSOFooter from "../components/SSOFooter";

const SSOLayout = ({ children }) => {
  const { isLoggedIn } = useSelector((state) => state.sso);
  const history = useHistory();
  const [isMobile, setIsMobile] = useState(false);

  const setMobile = (bool) => {
    setIsMobile(bool);
  };

  if (!isLoggedIn) {
    history.push("/login");
  }

  return (
    <div>
      {/* Header */}
      <div className="topbar-wrap">
        <SSOTopNav isMobile={isMobile} setMobile={setMobile} />
        <SSONavbar isMobile={isMobile} setMobile={setMobile} />
      </div>
      {/* Content */}
      <div className="page-content">
        <div className="container">{children}</div>
      </div>
      <AlertBox />
      <DialogBox />
      <SSOFooter />
    </div>
  );
};

export default SSOLayout;
