import React, { Component, Fragment } from "react";
import { useHistory } from "react-router-dom";
import RoleHRS from "../roles/RoleHRS";
import RoleUAS from "../roles/RoleUAS";
import RoleEVS from "../roles/RoleEVS";
import RoleSSO from "../roles/RoleSSO";

const SSORole = ({ data }) => {
  const history = useHistory();
  if (!data) {
    history.push("/dash");
  }

  const getRolePage = (data) => {
    const tag = (data && data.app_tag) || "uas";
    switch (tag) {
      case "uas":
        return <RoleUAS data={data} />;
      case "hrs":
        return <RoleHRS data={data} />;
      case "evs":
        return <RoleEVS data={data} />;
      case "sso":
        return <RoleSSO data={data} />;
    }
  };

  return (
    <div className="row">
      <div className="col-lg-12">
        <div className="token-information card card-full-height">
          <h3 className="sub-head bg-blueblack">
            <span style={{ fontSize: ".9rem", color: "#eee" }}>
              {data && data.app_tag.toUpperCase() + " PORTAL MENU "}
            </span>
          </h3>
          <div className="row no-gutters height-100">
            {/* Content */}
            {getRolePage(data)}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SSORole;
