import React, { Component, Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { setDatabox, setEvsdata, updateAlert } from "../../store/ssoSlice";
import { helperData } from "../../store/utils/helperData";
import {
  fetchEvsData,
  fetchEvsReceipt,
  fetchEvsRegister,
  fetchEvsUpdate,
} from "../../store/utils/ssoApi";
import EVSVote from "../apps/evs/EVSVote";
import "./RoleEVS.css";
import moment from "moment";
import {
  FcStatistics,
  FcAssistant,
  FcComboChart,
  FcRatings,
} from "react-icons/fc";
import EVSMonitor from "../apps/evs/EVSMonitor";
import EVSStrongroom from "../apps/evs/EVSStrongroom";
import EVSResult from "../apps/evs/EVSResult";
import Live from "../../assets/img/live.gif";
import EVSVoters from "../apps/evs/EVSVoters";
import EVSReceipt from "../apps/evs/EVSReceipt";
import ModalPage from "../components/ModalPage";
import EVSAdmin from "../apps/evs/EVSAdmin";
import Loader from "../../assets/img/loaderm.gif";

const RoleEVS = ({ data }) => {
  const { REACT_APP_API_URL: API_URL } = process.env;
  const { sso } = useSelector((state) => state);
  const { user } = sso;
  const dispatch = useDispatch();
  //const modules = user.roles.find(r => r.app_tag == 'evs').data;
  const [page, setPage] = useState(null);
  const [roles, setRoles] = useState([]);
  const [modules, setModules] = useState([]);
  const [activity, setActivity] = useState({
    mainload: true,
    register: false,
    vote: false,
  });

  const voteNow = async ({ id }, tag) => {
    const resp = await fetchEvsData(id, tag);
    if (resp.success) {
      const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			  Please click on the 'CHOOSE' button to make a choice of candidate or opinion.\n
			  Submit your all your choices by clicking on the 'SUBMIT FINAL VOTE' button above to cast your vote, THANK YOU!
			`;
      alert(info);
      dispatch(setEvsdata({ ...resp.data, allowAccess: true, id }));
      setPage("votenow");
      //setTimeout(dispatch(updateAlert({show:true,message:`WELCOME!, PLEASE CLICK ON 'CHOOSE' TO MAKE A PICK AND SUBMIT FINALLY!`,type:'info'})),2000)
    }
  };

  const resultNow = async ({ id, name, voters_count }, tag) => {
    const resp = await fetchEvsData(id, tag);
    if (resp.success) {
      const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			 The Election has ended and the Results are presented on the upcoming page.\n
			`;
      alert(info);
      dispatch(
        setEvsdata({ ...resp.data, name, voters_count, allowAccess: true, id })
      );
      setPage("result");
      //setTimeout(dispatch(updateAlert({show:true,message:`WELCOME!, PLEASE CLICK ON 'CHOOSE' TO MAKE A PICK AND SUBMIT FINALLY!`,type:'info'})),2000)
    }
  };

  const monitorNow = async ({ id, name }, tag) => {
    const resp = await fetchEvsData(id, tag);
    if (resp.success) {
      dispatch(setEvsdata({ ...resp.data, name, allowAccess: true, id }));
      setPage("monitor");
    }
  };

  const followNow = async ({ id, name }, tag) => {
    const resp = await fetchEvsData(id, tag);
    if (resp.success) {
      dispatch(setEvsdata({ ...resp.data, name, allowAccess: true, id }));
      setPage("strongroom");
    }
  };

  const viewRegister = async ({ id, name, allow_vmask }, tag) => {
    try {
      setActivity({ ...activity, register: true });
      const resp = await fetchEvsRegister(id);
      const resd = await fetchEvsData(id, tag);
      if (resp.success) {
        dispatch(
          setEvsdata({
            electors: [...resp.data.electors],
            aspirant: { ...(resd.success && resd.data) },
            name,
            allow_vmask,
            allowAccess: true,
            id,
          })
        );
        setActivity({ ...activity, register: false });
        setPage("list");
      }
    } catch (e) {
      console.log(e);
      alert(`${e.getMessage()}`);
      setActivity({ ...activity, register: false });
    }
  };

  const viewAdmin = async (dm) => {
    dispatch(setEvsdata({ ...dm, allowAccess: true }));
    setPage("admin");
  };

  const viewReceipt = async ({ id, name }, tag) => {
    const resp = await fetchEvsReceipt(id, tag);
    if (resp.success) {
      const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			 You successfully casted your vote, Find the confirmation on this Page.\n
			`;
      alert(info);
      dispatch(
        setEvsdata({ data: { ...resp.data }, allowAccess: true, id, name })
      );
      setPage("receipt");
    }
  };

  const getUpdates = async (tag) => {
    setActivity({ ...activity, mainload: true });
    try {
      if (tag) {
        const resp = await fetchEvsUpdate(tag);
        if (resp.success) {
          const dm = resp.data.find((r) => r.app_tag == "evs");
          if (dm) setModules(dm.data);
        } else {
          const dm = user.roles.find((r) => r.app_tag == "evs");
          if (dm) setModules(dm.data);
        }
        setActivity({ ...activity, mainload: false });
      }
    } catch (e) {
      setActivity({ ...activity, mainload: false });
    }
  };

  useEffect(() => {
    getUpdates(user && user.user && user.user.tag);
  }, [page]);

  const MainPage = () => (
    <>
      {activity.mainload && (
        <div
          style={{
            width: "100%",
            height: " 200px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <img
            src={Loader}
            style={{
              width: "40px",
              height: "40px",
              marginRight: 10,
            }}
          />
          <span
            style={{
              fontSize: 20,
              fontWeight: "bolder",
              textTransform: "uppercase",
            }}
          >
            Please wait ...
          </span>
        </div>
      )}

      {modules &&
        modules.map(
          (mod, i) =>
            (
              <div
                className="col-md-3 bg-red-500"
                key={i}
                style={{ overflow: "hidden" }}
              >
                {mod.on_test == 1 && (
                  <div className="trials">
                    <h3>ELECTION TRIALS</h3>
                    <p>This a Trial or Demo election !!!</p>
                  </div>
                )}
                <div className="token-info nitro">
                  <img
                    className={`fas fa-2x text-info`}
                    style={{ height: "100px" }}
                    src={`${API_URL}/photos/evs/?tag=logo&eid=${mod.id}`}
                  />
                  <div className="gaps-2x" />
                  <div>
                    <Link
                      title={mod.name}
                      className="btn btn-primary"
                      style={{ color: "#f8e0cd" }}
                    >
                      <b>{mod.tag.toUpperCase()} ELECTIONS </b>
                    </Link>
                  </div>
                  <small className="my-2 mx-3 center">
                    <b>{mod.name && mod.name.toUpperCase()}</b>
                  </small>

                  {/* Election Shortcuts */}
                  <div className="evs__icons">
                    {/*<span className="monitor"><FcAssistant className="monitor_icon"/></span>*/}
                    {mod.allow_monitor == 1 && mod.status == "STARTED" && (
                      <span
                        className="monitor"
                        title="Public Monitor"
                        onClick={() => monitorNow(mod, user && user.user.tag)}
                      >
                        <FcStatistics className="monitor_icon" />
                      </span>
                    )}
                    {mod.allow_monitor == 0 &&
                      mod.allow_monitor_ec == 1 &&
                      mod.status == "STARTED" &&
                      mod.ec > 0 && (
                        <span
                          className="monitor"
                          title="Public Monitor"
                          onClick={() => monitorNow(mod, user && user.user.tag)}
                        >
                          <FcStatistics className="monitor_icon" />
                        </span>
                      )}
                    {mod.allow_vip == 1 &&
                      ["STARTED", "STAGED", "HALTED"].includes(mod.status) && (
                        <span
                          className="monitor"
                          title="Strongroom Monitor"
                          onClick={() => followNow(mod, user && user.user.tag)}
                        >
                          <FcComboChart className="monitor_icon" />
                        </span>
                      )}
                    {mod.allow_vip == 0 &&
                      ["STARTED", "STAGED", "HALTED"].includes(mod.status) &&
                      mod.allow_vip_ec == 1 &&
                      mod.ec > 0 && (
                        <span
                          className="monitor"
                          title="Strongroom Monitor"
                          onClick={() => followNow(mod, user && user.user.tag)}
                        >
                          <FcComboChart className="monitor_icon" />
                        </span>
                      )}
                    {mod.allow_result == 1 && mod.status == "ENDED" && (
                      <span
                        className="monitor"
                        title="Final Results"
                        onClick={() => resultNow(mod, user && user.user.tag)}
                      >
                        <FcRatings className="monitor_icon" />
                      </span>
                    )}
                    {mod.allow_result == 0 &&
                      mod.allow_result_ec == 1 &&
                      mod.status == "ENDED" && (
                        <span
                          className="monitor"
                          title="Final Results"
                          onClick={() => resultNow(mod, user && user.user.tag)}
                        >
                          <FcRatings className="monitor_icon" />
                        </span>
                      )}
                    {/*['ENDED'].includes(mod.status) && <span className="monitor"><FcStatistics className="monitor_icon"/></span>*/}
                    {/* VOTE & BALLOT ACTION */}
                    {["STARTED"].includes(mod.status) &&
                      (mod.vote_status == 0 || mod.vote_status == null) &&
                      mod.voter != null &&
                      !activity.vote && (
                        <span
                          className="vote_cover btn-sm"
                          onClick={() => voteNow(mod, user && user.user.tag)}
                        >
                          Vote
                        </span>
                      )}

                    {["STARTED"].includes(mod.status) &&
                      mod.vote_status == 1 &&
                      mod.voter != null &&
                      !activity.vote && (
                        <span
                          className="vote_cover btn-sm"
                          onClick={() =>
                            viewReceipt(mod, user && user.user.tag)
                          }
                        >
                          Ballot
                        </span>
                      )}
                    {["STARTED"].includes(mod.status) &&
                      mod.vote_status == 1 &&
                      mod.voter != null &&
                      activity.vote && (
                        <span className="vote_cover btn-sm">
                          <img
                            src={Loader}
                            style={{
                              width: "20px",
                              height: "20px",
                            }}
                          />
                        </span>
                      )}
                  </div>

                  <div className="evs__icons" style={{ marginTop: "-40px" }}>
                    <hr />
                    {mod.ec > 0 && (
                      <span className="" onClick={() => viewAdmin(mod)}>
                        Admin Area
                      </span>
                    )}

                    {/* VOTER REGISTER ACTION */}
                    {!activity.register && (
                      <span
                        className=""
                        onClick={() => viewRegister(mod, user && user.user.tag)}
                      >
                        Voters Register
                      </span>
                    )}
                    {activity.register && (
                      <span
                        className=""
                        onClick={() => viewRegister(mod, user && user.user.tag)}
                      >
                        <img
                          src={Loader}
                          style={{
                            width: "20px",
                            height: "20px",
                          }}
                        />{" "}
                        Loading
                      </span>
                    )}
                  </div>

                  <div className="evs__info">
                    <span>
                      <b>Status:</b>{" "}
                      {mod.status == "STARTED" ? (
                        <img src={Live} className="" />
                      ) : (
                        mod.status
                      )}
                    </span>
                    {mod.vote_time && (
                      <span>
                        <b>Voted:</b> {moment(mod.vote_time).format("LLL")}
                      </span>
                    )}
                    {mod.start && (
                      <span>
                        <b>Opens:</b> {moment(mod.start).format("LLL")}
                      </span>
                    )}
                    {mod.end && (
                      <span>
                        <b>Closes:</b> {moment(mod.end).format("LLL")}
                      </span>
                    )}
                    {
                      <span>
                        <b>VOTERS:</b> {mod.voters_count}
                      </span>
                    }
                    {false && (
                      <span>
                        <b>TURNOUT:</b> {mod.voters_count}
                      </span>
                    )}
                  </div>
                  <br />
                </div>
              </div>
            ) || <h1 key={i}>NO ACTIVE ELECTIONS STAGED !</h1>
        )}
    </>
  );

  const PageRenderer = () => {
    switch (page) {
      case "votenow":
        return <EVSVote setPage={setPage} />;
        break;
      case "monitor":
        return <EVSMonitor setPage={setPage} />;
        break;
      case "strongroom":
        return <EVSStrongroom setPage={setPage} />;
        break;
      case "result":
        return <EVSResult setPage={setPage} />;
        break;
      case "list":
        return <EVSVoters setPage={setPage} />;
        break;
      case "receipt":
        return <EVSReceipt setPage={setPage} />;
        break;
      case "admin":
        return <EVSAdmin setPage={setPage} />;
        break;
      default:
        return <MainPage setPage={setPage} />;
    }
  };
  return (
    <>
      <PageRenderer />
      <ModalPage />
    </>
  );
};

export default RoleEVS;
